.navbar {
  position: fixed;
  top: 0;
  left: 0;
  background-color: transparent;
  width: 100%;
  z-index: 10;
  transition: all .5s; }
  .navbar.colorChange {
    background-color: #000;
    color: #fff;
    border-bottom: 1px solid #606060; }
    .navbar.colorChange .trademark {
      color: #fff;
      font-size: 1.75rem !important;
      transition: all .5s; }
      .navbar.colorChange .trademark:hover {
        color: #fff; }
    .navbar.colorChange .navbarMenuTitle {
      font-size: 1.15rem;
      transition: font-size .5s; }
    .navbar.colorChange .cart, .navbar.colorChange .mobileBar {
      font-size: 1.75rem;
      transition: font-size .5s; }
  .navbar .trademark {
    color: #121212;
    transition: all .5s; }
    @media (max-width: 767px) {
      .navbar .trademark {
        color: #fff; } }
    .navbar .trademark:hover {
      color: #000;
      transform: scale(1.1); }
      @media (max-width: 767px) {
        .navbar .trademark:hover {
          color: #fff; } }
  .navbar .lineItem {
    padding-top: 16px;
    padding-bottom: 16px; }
    @media (min-width: 575px) {
      .navbar .lineItem {
        display: none; } }
  .navbar .cart, .navbar .mobileBar {
    color: #fff;
    transition: font-size .5s; }
  .navbar .navbarMenu a {
    display: block;
    color: #fff; }
  @media (max-width: 575px) {
    .navbar .navbarMenu {
      position: absolute;
      top: 0;
      left: -120%;
      height: 100vh;
      width: 100%;
      flex-direction: column;
      background: #fff;
      padding-top: 68px;
      transition: all .5s; } }
  .navbar .navbarMenuTitle {
    transition: font-size .5s; }
    @media (min-width: 575px) {
      .navbar .navbarMenuTitle .material-icons {
        display: none; } }
  .navbar .back {
    display: none; }

.navbar.active {
  position: relative; }
  .navbar.active .logo {
    opacity: 0; }
    @media (min-width: 575px) {
      .navbar.active .logo {
        opacity: 1; } }
  .navbar.active .navbarMenu {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    width: 100vw;
    height: 100vh;
    flex-direction: column;
    padding-top: 68px;
    transition: all .5s; }
    @media (min-width: 575px) {
      .navbar.active .navbarMenu {
        position: initial;
        width: 100%;
        height: auto;
        flex-direction: row;
        padding-top: 0;
        background: transparent; }
        .navbar.active .navbarMenu a {
          display: block;
          color: #fff; } }
    .navbar.active .navbarMenu .lineItem {
      display: none; }
  .navbar.active .navbarMenuTitle {
    font-size: 1.5rem;
    color: #121212;
    border-bottom: 1px solid rgba(0, 0, 0, 0.38); }
    @media (min-width: 575px) {
      .navbar.active .navbarMenuTitle {
        border: none;
        font-size: 1rem; } }
    .navbar.active .navbarMenuTitle .material-icons {
      transform: rotate(0deg);
      transition: transform .5s; }
  .navbar.active .navbarMenuTitle.active .material-icons {
    transform: rotate(180deg);
    transition: transform .5s; }
  @media (min-width: 575px) {
    .navbar.active .navbarMenuTitle.active + .lineItem {
      display: none !important; } }
  .navbar.active .back {
    display: block;
    position: absolute;
    top: 5px;
    left: 10px;
    z-index: 3; }
    @media (min-width: 575px) {
      .navbar.active .back {
        display: none; } }
  .navbar.active .cart {
    display: block;
    z-index: 3;
    color: black; }
    @media (min-width: 575px) {
      .navbar.active .cart {
        color: #fff; } }

