input[type=checkbox] {
  cursor: pointer;
  width: 16px;
  height: 14px;
  position: relative; }

input[type=checkbox]:after {
  position: absolute;
  width: 18px;
  height: 18px;
  top: -2px;
  content: "";
  border: 1px solid #000;
  color: #fff;
  background: #fff;
  display: inline-block;
  visibility: visible;
  padding: 0px 4px;
  border-radius: 2px; }

input[type=checkbox]:checked:after {
  content: "✓";
  font-size: 12px;
  background-color: #0E8A00; }

