.contact {
  cursor: pointer; }
  .contact .material-icons {
    transform: rotate(0deg);
    transition: transform .5s; }
  .contact.active .contactInfo {
    padding-top: 16px;
    flex-direction: column;
    align-items: flex-start; }
  .contact.active .contactWrapper {
    flex-direction: column;
    align-items: start; }
  .contact.active .material-icons {
    position: absolute;
    top: 16px;
    right: 16px;
    transform: rotate(180deg);
    transition: transform .5s; }
  .contact.active .contactTextlen {
    white-space: normal;
    overflow: auto; }
  .contact.active .contactName {
    display: block; }
    .contact.active .contactName::after {
      content: ''; }
  @media (max-width: 767px) {
    .contact.active .contactEmail::after {
      content: ''; } }
  .contact.active .contactPhone {
    display: block; }
    .contact.active .contactPhone::before {
      content: ''; }
  .contact.active .contactAddress {
    padding-top: 16px;
    text-overflow: clip; }
  .contact .contactTitle {
    white-space: nowrap; }
  .contact .contactTextlen {
    white-space: nowrap;
    overflow: hidden; }
  @media (max-width: 767px) {
    .contact .contactName {
      display: none; } }
  .contact .contactName::after {
    content: '\00a0/\00a0'; }
  @media (max-width: 767px) {
    .contact .contactEmail {
      white-space: nowrap;
      overflow: hidden; } }
  @media (max-width: 575px) {
    .contact .contactEmail {
      text-overflow: ellipsis; } }
  @media (max-width: 767px) {
    .contact .contactEmail::after {
      content: '...'; } }
  @media (max-width: 767px) {
    .contact .contactPhone {
      display: none; } }
  .contact .contactPhone::before {
    content: '\00a0/\00a0'; }
  .contact .contactAddress {
    text-overflow: ellipsis; }

